body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.card {
  background: #fff;
  border-radius: 2px;
  height: 110px;
  margin: 5px;
  position: relative;
  float: left;
  width: 145px;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
}

.card:hover {
  box-shadow: 0 9px 18px #999, 0 3px 6px #999;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
  background:#6CADDC;
}

.card > .img-container > img {
  width: 50%
}

.card > .content {
  padding-left: 1rem;
  padding-right: 1rem;
  font-size: 15px;
}

.card > .content > ul {
  list-style-type: none;
}

.card > .content > ul > li {
  margin-top: 18px;
}

.card > .remove {
  color: #ff1744;
  font-size: 48px;
  position: absolute;
  top: -20px;
  right: 5px;
  cursor: pointer;
}

.card > .remove:hover {
  color: #d50000;
}

@font-face {
  font-family: simpson;
  src: url("/clickygame/static/media/Simpsonfont DEMO.42e79bc9.otf");
}

.navSimpson {
  background:#FFD90F;
}

.navSimpsonBrand {
  font-family: simpson;
  color: royalblue;
  font-size: 24px;
}

.scoreSimpsons {
  font-family: simpson;
  color: royalblue;
  font-size: 16px;
}

.scoreBlack {
  color: black;
  margin-left: 4px;
  margin-right: 15px;
}
